import Pozadi from "../obrazky/Pozadi.jpeg";
import "../styl/O_nas.css";
function O_nas() {
  return (
    <div className="o_nas">
      <div
        className="o_nasTop"
        style={{ backgroundImage: `url(${Pozadi})` }}
      ></div>
      <h1>O ns</h1>  
      <div className="o_nasBottom">
        <p>
          Nai pekrnu najdete v srdci Libie. Mete ns navtvit osobn nebo si 
          objednat nae produkty online s doruenm a k vm dom.
        </p>
        <h2>Nae hodnoty</h2>
        <ul>
          <li> Pouvme pouze nejkvalitnj suroviny</li>
          <li> Kad vnoka je pletena run s lskou</li>
          <li> Dodrujeme tradin postupy</li>
          <li> Zakldme si na osobnm pstupu k zkaznkm</li>
        </ul>

      </div>
    </div>
  );
}

export default O_nas;